  dacantidadh(id, arra): String {
  const re = '0';
  for (let i2 = 0; i2 < arra.length; i2++) {
    const cad = arra[i2];
    if (cad.split(',')[0].trim() === id) {
      return cad.split(',')[1].split(':')[1].trim();
      break;
    }

  }

  return re;
  

  }

